      "alertText": "\r\n        <p><strong>Tilbury Town - Tilbury Riverside (For Gravesend Ferry)</strong></p>\r\n        <p>Ensign Bus 99 runs from Tilbury Town (Platform 1 side) to connect with the Gravesend Ferry.</p>\r\n        <p><strong>Tilbury - Gravesend Ferry Tickets</strong></p>\r\n        <p>Tickets marked \"TILBRY &amp; GRAVESND\" are valid for the bus between Tilbury Town and Tilbury Riverside and on connecting rail services at Gravesend, but are <strong>not valid for the Ferry itself.</strong></p>\r\n        <p>Separate ferry fares must be paid on board the ferry. More details can be found on the <a href=\"https://www.thurrock.gov.uk/ferry-services/tilbury-to-gravesend-ferry-service\"><strong>Thurrock Council website.</strong></a></p>\r\n        <p></p>\r\n    ",
